import React, { useState, useCallback } from 'react';
import styles from './index.module.scss';
import posts from '../../posts.json';

export const Catalog = () => {
  return <div
    className={styles.content}
  >
    {posts.map((post, index) =>
      <div
        key={post.id + index}
        className={styles.container}
      >
          catalog
      </div>
    )}
  </div>
};
