import React, { useState, useCallback } from 'react';
import { useParams } from 'react-router-dom';
import styles from './index.module.scss';
import posts from '../../posts.json';
import { MarkdownRender } from '../markdown_render';

export const Content = () => {
  const { id } = useParams<{ id: string }>();
  const [isShowScrollBar, setIsShowScrollBar] = useState(false);
  const wheel = useCallback(() => {
    let timer: any;
    return () => {
      clearTimeout(timer);
      setIsShowScrollBar(true);
      console.log(1)
      timer = setTimeout(() => {
        console.log(2)
        setIsShowScrollBar(false);
      }, 333);
    }
  }, [setIsShowScrollBar]);

  const post = posts[parseInt(id)-1];
  if(!post){
    return null;
  }

  return <div
    className={`${styles.content} ${isShowScrollBar ? styles.showScrollBar : styles.hideScrollBar}`}
  // onWheel={wheel}
  >
      <div
        className={styles.container}
      >
        <img alt='profile' src={post.profile} />
        <div className={styles.title}>{post.title}</div>
        <MarkdownRender children={post.content} />
      </div>
    {/* {posts.map((post, index) =>
      <div
        key={post.id + index}
        className={styles.container}
      >
        <img alt='profile' src={post.profile} />
        <div className={styles.title}>{post.title}</div>
        <MarkdownRender children={post.content} />
      </div>
    )} */}
  </div>
};
